/*-------------RESET.CSS----------------*/

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,
blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,
// em,
img,ins,kbd,q,s,samp,small,strike,
// strong,
sub,sup,tt,var,b,u,center,dl,dt,dd,ol,ul,li,fieldset,form,label,
legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,
details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,
time,mark,audio,video, input {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
    display: block;
}
body {
    line-height: 1;
}
ol,
ul {
    // list-style: none;
    margin-left: 2rem;
}
blockquote,
q {
    quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
    content: "";
    content: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}

// Number input type's arrows disappeared
/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

/* Firefox */
input[type="number"] {
    -moz-appearance: textfield;
}

input {
    background-color: transparent;
}

input[type="date"]::-webkit-calendar-picker-indicator {
    font-size: 1.4rem;
    &:hover {
        background-color: transparent;
    }

    &:disabled {
        display: none;
    }
}

// NOTIFICATION

.notification-message {
    white-space: pre-line;
}

// BOOTSRAP

.btn {
    font-size: 1.4rem;
    padding: 1rem 1.5rem;
}
.dropdown-menu {
    font-size: 1.4rem;

    .dropdown-item {
        padding: 1rem 1.5rem;
        &:hover {
            background-color: rgba(0, 0, 0, 0.103);
        }
    }
}

// TABS
.nav-tabs {
    border-bottom: none;
}

.nav-item {
    padding: 1.5rem 1rem;
}
